import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import './StartModal.css';

const StartModal = ({ className, onContinue }) => {
  return (
    <div className={classNames(className, "start-modal-main")}>
      <section className="start-modal-content">
        <h2 className="start-modal-title">
          {"Shanghai Shnagon"}
        </h2> 
        <p className="start-modal-text">
          {"Shanghai Shnagon ist ein Romanprojekt. Du liest die ersten 24 Kapitel in einer dynamisch erstellten Reihenfolge. Sie beruht zur einen Hlfte auf Zufall und zur anderen auf inhaltlichen Zusammenhngen. Mehr Informationen zum Algorithmus findest du "}
          <Link to={`${process.env.APP_URL}/about`}  target="_blank">hier. </Link>
          {"(Achtung! Auf der Seite, zu die der Link fhrt, sind Spoiler enthalten.)"}
        </p>
        <button onClick={ () => onContinue() } className="start-confirmationButton">
          {"Ok, weiter im Text"}
        </button>
      </section>
    </div>
  );
};

export default StartModal;


